.Enter_button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(138, 57, 237);
    color: rgb(225, 220, 230);
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 10px groove rgb(12, 60, 255);
    font-family: cursive;
    font-size: 20px;
    box-shadow: 0px 0px 20px rgb(19, 176, 255);
    animation: appearEnterButton 2s cubic-bezier(.23, .83, .85, 1.3) forwards, rotateButton 3s 2s ease-in-out infinite;
    cursor: pointer;
    margin-top: -1000px;
    font-weight: bold;
}
.RefreshQuiz{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(138, 57, 237);
    color: rgb(225, 220, 230);
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 10px groove rgb(12, 60, 255);
    font-family: cursive;
    font-size: 20px;
    box-shadow: 0px 0px 20px rgb(19, 176, 255);
    cursor: pointer;
    font-weight: bold;
}
body {
    display: grid;
    place-items: center;
    min-height: 100vh;
    background-image: url('images/New-blog-graphic.jpg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    color: white;
}

/* questions styling */
h1 {
    text-align: center;
    font-family: cursive;
    font-size: 40px;
    color: rgb(87, 2, 87);
}

.container {
    padding: 20px 100px;
    border: 1px solid rgb(244, 175, 255);
    box-shadow: 0px 0px 10px pink;
    display: flex;
    flex-direction: column;
    gap: 50px;
    backdrop-filter: blur(7px);
}

.question {
    /* border: 1px solid red; */
    padding: 3px;
    /* backdrop-filter: blur(3px); */
    font-size: 18px;
}

.options {
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.option {
    /* border: 1px solid black; */
    display: flex;
    gap: 2%;
    flex-direction: rows;
    padding: 2px 2px 2px 0px;
}

.option .s_no {
    /* border: 1px solid black; */
    font-size: 20px;
    /* background-color: rgb(123, 0, 255, 0.3); */
    border: 1px solid white;
    border-radius: 5px;
    padding: 5px;
    cursor: pointer;
}

.option .choice {
    display: flex;
    align-items: center;
    width: 100%;
    /* background-color: rgb(88, 94, 255, 0.3);s */
    border-radius: 5px;
    border: 1px solid white;
    padding-left: 5px;
    cursor: pointer;
}


.next,
.prev {
    position: absolute;
    font-size: 50px;
    display: block;
    font-weight: bolder;
    cursor: pointer;
}

.next {
    top: 45vh;
    right: 25px;
}

.prev {
    top: 45vh;
    left: 25px;
}

.btn {
    display: flex;
    justify-content: center;
    align-items: center;
}

button {
    margin: 30px;
    cursor: pointer;
    padding: 5px 30px;
    background-image: linear-gradient(to right top, #dbb4ff, #a870d8, #803db4, #591f80, #460e66);;
    border-radius: 20px;
}
.greet{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Animatoins */
@keyframes appearEnterButton {
    0% {
        margin-top: -1000px;
    }

    100% {
        margin-top: 0px;
    }
}

@keyframes rotateButton {
    0% {
        transform: rotateZ(0deg);
    }

    100% {
        transform: rotateZ(360deg);
    }
}